<template>
  <div id="views-theshoppingcart_form">
    <h3 class="my-4">
      
    </h3>

    <b-card>
      <b-row>
        <b-col cols="6">
          <validation-observer
            v-slot="{ passes }"
            ref="observer"
          >
            <b-form
              v-if="show"
              @submit.prevent="passes(createOrder)"
            >
              <validation-provider
                v-slot="validationContext"
                name=""
                rules="required|email"
              >
                <b-form-group
                  id="input-email"
                  label=""
                  label-for="input-email"
                >
                  <b-form-input
                    id="input-email"
                    v-model="form.user.email"
                    :state="getValidationState(validationContext)"
                    placeholder=""
                    required
                  />
                  <b-form-invalid-feedback id="input-1-live-feedback">
                    {{ validationContext.errors[0] }}
                  </b-form-invalid-feedback>
                </b-form-group>
              </validation-provider>

              <validation-provider
                v-slot="validationContext"
                name=""
                rules="required"
              >
                <b-form-group
                  id="input-name"
                  label=""
                  label-for="input-name"
                >
                  <b-form-input
                    id="input-name"
                    v-model="form.user.name"
                    :state="getValidationState(validationContext)"
                    placeholder=""
                    required
                  />
                  <b-form-invalid-feedback id="input-1-live-feedback">
                    {{ validationContext.errors[0] }}
                  </b-form-invalid-feedback>
                </b-form-group>
              </validation-provider>

              <validation-provider
                v-slot="validationContext"
                name=""
                rules="required"
              >
                <b-form-group
                  id="input-tel"
                  label=""
                  label-for="input-tel"
                >
                  <b-form-input
                    id="input-tel"
                    v-model="form.user.phone"
                    :state="getValidationState(validationContext)"
                    placeholder=""
                    required
                  />
                  <b-form-invalid-feedback id="input-1-live-feedback">
                    {{ validationContext.errors[0] }}
                  </b-form-invalid-feedback>
                </b-form-group>
              </validation-provider>

              <validation-provider
                v-slot="validationContext"
                name=""
                rules="required"
              >
                <b-form-group
                  id="input-address"
                  label=""
                  label-for="input-address"
                >
                  <b-form-input
                    id="input-address"
                    v-model="form.user.address"
                    :state="getValidationState(validationContext)"
                    placeholder=""
                    required
                  />
                  <b-form-invalid-feedback id="input-1-live-feedback">
                    {{ validationContext.errors[0] }}
                  </b-form-invalid-feedback>
                </b-form-group>
              </validation-provider>

              <validation-provider
                v-slot="validationContext"
                name=""
                rules="max:300"
              >
                <b-form-group
                  id="input-msgs"
                  label=""
                  label-for="input-msg"
                >
                  <b-form-textarea
                    id="input-msgs"
                    v-model="form.message"
                    :state="getValidationState(validationContext)"
                    max-rows="6"
                    placeholder=""
                    rows="3"
                  />
                  <b-form-invalid-feedback id="input-1-live-feedback">
                    {{ validationContext.errors[0] }}
                  </b-form-invalid-feedback>
                </b-form-group>
              </validation-provider>

              <b-button
                class="float-right"
                size="sm"
                type="submit"
                variant="primary"
                @click="passes(createOrder)"
              >
                
              </b-button>
            </b-form>
          </validation-observer>
        </b-col>
        <b-col
          cols="6"
          class="d-flex justify-content-center mt-5"
        >
          <v-svg-form />
        </b-col>
      </b-row>
    </b-card>
  </div>
</template>

<script>
import { apiCustomerCreateOrder } from '../plugins/axios'
import VSvgForm from '../components/VSvgForm'

export default {
  name: 'TheCustomerShoppingCartForm',
  components: {
    VSvgForm
  },
  data () {
    return {
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      },
      show: true
    }
  },
  methods: {
    async createOrder () {
      try {
        const order = this.form
        const response = await apiCustomerCreateOrder({ data: order })
        if (!response.data.success) return
        this.$router.push(`/customer_checkout/${response.data.orderId}`)
      } catch (e) {
        console.log(e)
      }
    },
    getValidationState ({ dirty, validated, valid = null }) {
      return dirty || validated ? valid : null
    }
  }
}
</script>
